import { useState } from "react"

type UpdatePaddlerInfoType = {
    paddler: any,
    updatePaddlerModal: (flag: boolean) => void,
    updatePaddler: (paddler: any) => void
}


const UpdatePaddlerInfo: React.FC<UpdatePaddlerInfoType> = ({ paddler, updatePaddlerModal, updatePaddler }) => {
    const [paddlerData, setPaddlerData] = useState<any>(paddler);

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        updatePaddlerModal(false);
        updatePaddler(paddlerData);
    }

    const updateWeight = (weight: string) => {
        let data = {
            ...paddlerData,
            weight: parseInt(weight)
        };
        setPaddlerData(data);
    }

    return (
        <>
            <div
                className={`
                            justify-center items-center flex overflow-x-hidden overflow-y-auto 
                            fixed inset-0 z-50 outline-none focus:outline-none
                            `}>
                <div className="relative w-auto my-6 mx-auto max-w-3xl">
                    <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                        <form onSubmit={handleSubmit}>
                            <div className="flex items-start justify-between p-5 border-b border-solid border-slate-200 rounded-t">
                                <h3 className="text-xl font-semibold">
                                    Edit details for {paddlerData.id}
                                </h3>
                                <button
                                    className={`
                                            p-1 ml-auto bg-transparent border-0 text-black opacity-5 
                                            float-right text-3xl leading-none font-semibold outline-none 
                                            focus:outline-none
                                            `} onClick={() => updatePaddlerModal(false)}>
                                    <span className="bg-transparent text-black opacity-5 h-6 w-6 text-2xl block outline-none focus:outline-none">
                                        X
                                    </span>
                                </button>
                            </div>

                            <div className="relative p-6 flex-auto">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="weight">
                                    Weight in kg
                                </label>
                                <input id="weight" type="text"
                                    className={`
                                                shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 
                                                leading-tight focus:outline-none focus:shadow-outline
                                            `} placeholder="Weight"
                                    value={paddlerData.weight}
                                    onChange={(e) => updateWeight(e.target.value)}
                                />
                            </div>

                            <div className="flex items-center justify-end p-6 border-t border-solid border-slate-200 rounded-b">
                                <button
                                    className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                                    type="button"
                                    onClick={() => updatePaddlerModal(false)}
                                >
                                    Close
                                </button>
                                <button
                                    className="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                                    type="submit"
                                >
                                    Save Changes
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
    );
}

export default UpdatePaddlerInfo;